import styled from 'styled-components'

const Button = styled.button`
background-color: #488068;
font: inherit;
padding: 0.5rem 2rem;
color: #182F25 !important;

:hover {
    background-color: #DFF2D8;
    color: #488068 !important;
    transition: ease 0.4s;
}
`    
export default Button;